}

.ug-slider-preloader.ug-loader3{
  width:38px;
  height:38px;
  background-image:url('../images/loader-white3.gif');
}

.ug-slider-preloader.ug-loader3.ug-loader-black{
  background-image:url('../images/loader-black3.gif');
}

.ug-slider-preloader.ug-loader4{
