.--------------STRIP_PANEL------------------{}

.ug-gallery-wrapper .ug-strip-panel{
  position:absolute;
  background-color:#000000;
  z-index:2;
}

.ug-strip-panel .ug-strip-arrow{
  position:absolute;
  cursor:pointer;
}

.ug-strip-panel .ug-strip-arrow.ug-button-disabled{
  cursor:default;
}


.ug-strip-panel .ug-strip-arrow-tip{
